body{
    background-image: url('../public/worldmap.jpg');
    background-size:auto;
}

.welcome-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #ffffff;
    font-family: Arial, sans-serif;
}
  
#welcomeTitle {
    font-size: 3rem;
    margin-bottom: 1rem;
    font-family: 'Times New Roman', Times, serif;
    display: inline-flex;
}

.titleSpace {
    font-size: 1.5em;
    width: 0.6em;
}

.titleLetter {
    font-size: 1.5em;
    width: 0.77em;
    text-align: center;
    transition: transform 0.25s ease;
}
  
.titleLetter.hovered {
transform: scale(1.5);
}

.titleLetter.next, .titleLetter.before{
    transform: scale(1.2);
}

/* from https://codepen.io/t_afif/pen/VwxbdpV */
button {
    --b: 3px;   
    --s: 0em; 
    --c: #000000;

    padding: calc(.05em + var(--s)) calc(.3em + var(--s));
    --_p: var(--s);
    background:
        conic-gradient(from 90deg at var(--b) var(--b),#0000 90deg,var(--c) 0)
        var(--_p) var(--_p)/calc(100% - var(--b) - 2*var(--_p)) calc(100% - var(--b) - 2*var(--_p));
    transition: .15s linear;
    outline: var(--b) solid #0000;
    outline-offset: .2em;
}

button:hover, button:focus-visible{
    --_p: 0px;
    outline-color: var(--c);
    outline-offset: .05em;
}

button {
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: bold;
    font-size: 2rem;
    cursor: pointer;
    border: none;
    margin: .5em;
    padding: 10px;
}

.dateSelector {
    display: flex;
    align-items: center;
    gap: 10px;
}

.selectMonth, .selectDay {
    appearance: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 5px 10px;
    font-size: 16px;
    cursor: pointer;
    outline: none;
    background-color: #fff;
}

.selectMonth:hover, .selectDay:hover {
    background-color: #ccc;
}

#goButton{
    width: 75px;
    height: 50px;
    font-size: 1.7rem;
}

#orSpan, #chooseDay{
    margin: 2px;
    font-size: 30px;
}

.dateSelector select option {
    background-color: #fff;
}

  
